  border: none;
  color: #fff;
  font-family: "Roboto";
  font-size: 2rem;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}
.form__input:focus {
  outline: none;
}
.form__input::placeholder {
  color: rgba(255, 255, 255, 0.3);
}
.form__input:not([type=checkbox]) {
  border-bottom: 0.1rem solid #FFF;
}
.form__policy {
  cursor: pointer;
  margin: 0;
  color: #FFF;
  font-family: "Roboto";
  font-size: 1.6rem;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  padding-left: 2rem;
}
.form__policy > a {
  color: #235EED;
  text-decoration-line: underline;
}
.form__btn {
  text-align: center;
}

.scheme {
  padding-bottom: 12rem;
}
.scheme__title::before {
  content: "Схема работ";
}
.accord__item {
  padding: 1rem 0;
}
.accord__item:nth-child(even) .accord__line {
  margin-left: -2rem;
}
.accord__item:nth-child(even).active .accord__title::before {
  transform: rotate(90deg);
}
.accord__item:nth-child(even) .accord__title {
  order: -1;
  text-align: left !important;
  padding-left: 12rem;
  padding-right: 3rem !important;
}
.accord__item:nth-child(even) .accord__title::before {
  left: unset;
  right: 4.5rem;
  background-image: url("./../img/triangle-right.svg");
}
.accord__item:nth-child(even) .accord__desc {
  grid-column: 6/7 span;
}
.accord__item:nth-child(odd) .accord__line {
  margin-right: -2rem;
}
.accord__item.active .accord__desc {
  margin-bottom: 6rem;
}
.accord__item.active .accord__title::before {
  transform: rotate(-90deg);
}
.accord__row {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 2rem;
}
.accord__line {
  grid-column: 7 span;
  position: relative;
}
.accord__line::before {
  content: "";
  position: absolute;
  left: 0;
  top: 2.4rem;
  transform: translateY(-50%);
  width: 100%;
  height: 0.1rem;
  background-color: #183475;
}
.accord__header {
  cursor: pointer;
  padding: 1rem 0;
}
.accord__title {
  grid-column: 5 span;
  position: relative;
  margin: 0;
  grid-column: 5 span;
  padding-left: unset;
  padding-right: 12rem;
  color: #403D3A;
  font-family: "Oswald";
  font-size: 3.2rem;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  text-transform: uppercase;
  text-align: right;
}
.accord__title::before {
  content: "";
  position: absolute;
  top: 1.1rem;
  left: 5rem;
  transform: translate(-50%, 0);
  width: 2.5rem;
  height: 3rem;
  background-image: url("./../img/triangle.svg");
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  transform-origin: center;
  transition: transform 0.1s ease-out;
}
.accord__desc {
  grid-column: 7 span;
  margin: 0;
  color: #403D3A;
  font-family: "Roboto";
  font-size: 2.8rem;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  max-height: 0;
  overflow: hidden;
  will-change: max-height;
  transition: max-height 0.3s ease-out;
}

.about {
  padding-bottom: 12rem;
}
.about__container {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  align-items: center;
}
.about__info {
  position: relative;
  grid-column: 6 span;
}
.about__images {
  grid-column: 6 span;
  height: 96rem;
  width: 100%;
}
.about__img {
  position: relative;
  height: 100%;
  width: 100%;
}
.about__img img {
  position: absolute;
  inset: 0;
  height: 100%;
  width: 100%;
}

.about-info__content {
  position: relative;
  z-index: 5;
  padding: 7.6rem 3.2rem;
  background-color: #183475;
  color: #FFF;
  font-family: "Roboto";
  font-size: 2.8rem;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}
.about-info__content > p {
  margin: 0;
}
.about-info__content > p:not(:last-child) {
  margin-bottom: 3rem;
}
.about-info__img {
  position: absolute;
  z-index: 1;
  right: -42rem;
  top: -3.4rem;
  height: calc(100% + 7.4rem);
  width: 58rem;
}
.about-info__img img {
  width: 100%;
  height: 100%;
}

.footer {
  padding: 8.3rem 0;
  padding-bottom: 3.7rem;
  background-color: #403D3A;
}
.footer__container {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 2rem;
}
.footer__logo img {
  width: 100%;
}
.footer__col {
  grid-column: 3 span;
  display: flex;
}
.footer__col--centered {
  justify-content: center;
}
.footer__item:not(:last-child) {
  margin-bottom: 3rem;
}
.footer__link {
  color: #FFF;
  font-family: "Roboto";
  font-size: 1.5rem;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  text-align: left;
}
@media (any-hover: hover) {
  .footer__link:hover {
    text-decoration: underline;
  }
}
.footer__address {
  grid-column: 2 span;
}
.footer__address p {
  margin: 0;
  color: #FFF;
  font-family: "Roboto";
  font-size: 1.5rem;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}
.footer__address p:not(:last-child) {
  margin-bottom: 3rem;
}

.footer-socials__item--tel {
  margin-bottom: 1.5rem;
}
.footer-socials__item--tel a {
  font-size: 2.4rem;
}
.footer-socials__item--call {
  display: flex;
  align-items: center;
  margin-bottom: 2.4rem;
}
.footer-socials__item--call svg {
  margin-right: 0.5rem;
}
.footer-socials__item--call a {
  font-size: 1.2rem;
}
.footer-socials__item--mail {
  font-size: 1.5rem;
  margin-bottom: 1.8rem;
}
.footer-socials__item--tg, .footer-socials__item--wh {
  display: inline-block;
  width: 5rem;
  height: 5rem;
  border-radius: 100%;
}
.footer-socials__item--tg svg, .footer-socials__item--wh svg {
  width: 100%;
  height: 100%;
}
.footer-socials__item--tg {
  margin-right: 4.2rem;
}